.page {
  scroll-behavior: smooth;
  overflow-y: scroll;
  scrollbar-gutter: stable;

  font-family: Manrope, sans-serif;
  font-size: 16px;
  line-height: 140%;
  color: $main-text-color;
  letter-spacing: 0.15px;

  &__body {
    min-width: 320px;
    margin: 0;
  }

  &__menu {
    pointer-events: none;

    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    transform: translateX(-100%);

    opacity: 0;

    transition: all 0.3s;

    &:target {
      pointer-events: all;
      transform: translateX(0);
      opacity: 1;
    }
  }
}

.page:has(.page__menu:target) {
  overflow: hidden;
}

button {
  @include hover(background-color, $primary-button-hover-color);
}
